<template>
  <div id="app">
    <form @submit.prevent="runParser">
      <label for="coin">Coin</label>
      <input id="coin" type="text" v-model="coinUpperCased">
      <button type="submit">Parse</button>
    </form>
    Identical accounts: {{ sameAccountsCount }}
    <div class="spinner" :class="{ isActive: loading }">
      <img src="./assets/spinner.gif" alt="spinner" class="spinner-img">
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import _ from 'lodash';

export default {
  name: 'app',
  data: () => ({
    coin: '',
    sameAccountsCount: 0,
    GICHolders: [],

    loading: false,
  }),
  methods: {
    async runParser() {
      this.loading = true;

      const GICHolders = await this.getAllHolders(1, [], 'GIC');
      const parcedCoinHolders = await this.getAllHolders(1, [], this.coin);
      _.each(parcedCoinHolders, (account) => {
        _.find(GICHolders, { accountId: account.accountId }) ? this.sameAccountsCount++ : '';
      });

      this.loading = false;
    },
    async getHolders(page, coin = 'GIC') {
      return axios.get(`https://bitsharescan.com/api/asset/BRIDGE.${coin}/holders?page=${page}perPage=50`);
    },
    async getAllHolders(pageCounter = 1, holders = [], coin = 'GIC') {
      const { data } = await this.getHolders(pageCounter, coin);
      holders.push(...data);
      if (data.length === 50) {
        await this.getAllHolders(++pageCounter, holders, coin);
      }
      return holders;
    },
  },
  computed: {
    coinUpperCased: {
      get() {
        return this.coin;
      },
      set(val) {
        this.coin = val.toUpperCase();
      },
    },
  },
};
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.spinner {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  opacity: .8;
}
.spinner.isActive {
  display: block;
}
.spinner-img {
  height: 100%;
}
</style>
